.box {
  margin: 0;
  padding: 0;
  display: inline-block;
  width: 10vh;
  height: 10vh;

  &-white {
    background-color: white;
  }

  &-black {
    background-color: black;
  }

  &-wrapper {
    margin: 0;
    padding: 0;
    line-height: 0;

    max-width: 40vh;
    max-height: 40vh;
    display: inline-block;
    border: 1px solid red;
  }
}
