$sz: 7vw;

* {
  margin: 0;
  padding: 0;
}

.img-map {
  width: $sz;
  height: $sz; // border-right: 2px solid black;
}

.text-number {
  font-size: 50px;
  font-weight: bold;
  width: $sz;
  text-align: center;
}

.img-wrapper {
  display: inline-block;
  max-width: $sz;
}

.right {
  border-right: 2px solid black;
}

.key {
  margin-top: 20px;
  border: 2px solid black;
}

.key-wrapper {
  display: flex;
  flex-direction: row;
  min-width: 70vw;
  align-items: center;
  justify-content: center;
}
