.cross {
  position: fixed;
  left: calc(50vw - 10vh);
  top: calc(50vh - 2.5vh);

  width: 20vh;
  height: 5vh;
  background-color: black;

  &::after {
    display: block;
    width: 20vh;
    height: 5vh;
    background-color: black;
    transform: rotateZ(90deg);
    content: "";
  }

  &-wrapper {
    opacity: 0;
  }
}

.button {
  height: 40vh;
  width: 40vh;
  color: white;
  font-size: 10vh;
  border: none;

  border-radius: 100%;

  &-yes {
    background-color: green;
  }

  &-no {
    background-color: red;
  }
  &-wrapper {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: row;
    opacity: 0;
  }

  &:hover {
    cursor: pointer;
  }

  &:focus {
    border: none;
    outline: none;
  }

}
